import { bdrToStyle } from '../generateStyle';

describe('Utils.bdrToStyle', () => {
  it('Handle bdr correctly', () => {
    const bdr = {
      top: {
        sz: 1.3333333333333333,
        space: 0,
        color: '#ffffff',
      },
      left: {
        val: 'single',
        space: 0,
        color: '#ffffff',
      },
      bottom: {
        val: 'single',
        sz: 1.3333333333333333,
        space: 0,
        color: '#ffffff',
      },
      right: null,
    };
    const style = bdrToStyle(bdr);
    expect(style.borderLeft).toBe('solid #ffffff 1px');
    expect(style.borderTop).toBe('solid #ffffff 1.3333333333333333px');
    expect(style.borderBottom).toBe('solid #ffffff 1.3333333333333333px');
    expect(style.borderRight).toBeUndefined();
  });
});
